	data() {
		return {
			location: '../../static/location.png',
			serverPhone: '020-38473496',
			scanner: '../../static/chaxun.png',
			mytoolbarList: [{
					url: '../addEntrust/main',
					text: '业务委托',
					img: '../../static/yewu.png'
				},
				{
					url: '../HM-search/HM-search',
					text: '评估进度',
					img: '../../static/jindu.png'
				},
				{
					url: '../myqr/main',
					text: '微信付款',
					img: '../../static/fukuan.png'
				}

				// {
				// 	url: '../myqr/main',
				// 	text: '融资服务',
				// 	img: '../../static/rongzi.png'
				// },
				// {
