			]
		}
	},
	// 页面分享
	onShareAppMessage: function() {
		return {
			title: '腾业服务',
			desc: '',
			path: '/pages/server/main'
		}
	},
	methods: {
		handling(){
			uni.showToast({
				icon:'none',
				title: '该功能还未上线！',
				duration: 600
			});
		},
		
		doSendLocation() {
			let that = this;
			uni.showLoading({
				title: '位置发送中...',
			})
			uni.getLocation({
				success: async function(res) {
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
					let requestData = {
						openId: wx.getStorageSync('userSession').token,
						createTime:new Date(),
						latitude: res.latitude,
						longitude: res.longitude
					}
					let response = await that.$post1('address/add', requestData);
					// 隐藏加载框
					uni.hideLoading();
					if(response.code - 200 == 0 ){
						uni.showToast({
							title: '发送位置成功',
							duration: 600
						});
					}else{
						uni.showToast({
							icon:'none',
							title: '发送位置失败',
							duration: 600
						});
					}
				},
				fail: function(res1) {
					// 隐藏加载框
					uni.hideLoading();
					uni.showToast({
						icon:'none',
						title: '定位失败',
						duration: 600
					});
				}
			});
		},
		doScanner() {
			// 允许从相机和相册扫码
			uni.scanCode({
				success: function(res) {
					/* let resstr = (res.result+"").replace('?','[$');
					 resstr = resstr.replace('=','$]'); */
					 let resstr = (res.result+"").split('=')[1]
					 uni.navigateTo({
					  url: '../scannerCode/main?title=' + resstr
					 })
				}
			});
		},
		toPage(url) {
			uni.navigateTo({
				url: url
			})
		},
		doPhone(phone) {
			uni.makePhoneCall({
				phoneNumber: phone //仅为示例
			});
		}
	}
}
